/* general */
@font-face {
    font-family: 'Poppins';
    src: url('../fonts/Poppins/Poppins-Regular.ttf');
    font-weight: normal;
}

@font-face {
    font-family: 'Poppins';
    src: url('../fonts/Poppins/Poppins-ExtraBold.ttf');
    font-weight: bold;
}

@font-face {
    font-family: 'Open-sans';
    src: url('../fonts/open-sans/OpenSans-Regular.ttf');
}

:root {
    --primaryColor: #222222;
    --secondaryColor: #fa5b0f;
}

.secondary-color {
    color: var(--secondaryColor);
}

* {
    max-width: 100%;
    font-family: 'Open-sans', sans-serif;
    color: #f9f9f9;
}

/* highlight */
::selection {
    background-color: var(--secondaryColor);
    color: #f9f9f9;
}

/* custom scrollbar */
::-webkit-scrollbar-thumb {
    background-color: var(--secondaryColor);
    border-radius: 1rem;
}

::-webkit-scrollbar {
    width: .5rem;
    color: var(--secondaryColor);
    background-color: #787878;
}

::-webkit-scrollbar-track {
    border-radius: .5rem;
    box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}

body {
    overflow: hidden;
    margin: 0;
    padding: 0;
    background: var(--primaryColor);
}

h1 {
    font-size: 2rem;
}

h1,
h2,
h3 {
    
    color: #efefef;
    font-family: 'Poppins', 'sans-serif';
    font-weight: bold;
    text-transform: uppercase;
}

a {
    text-decoration: none;
    display: inline-block;
}

/* custom line divider that functions like <hr /> */
.divider {
    width: 100%;
    height: 1px;
    margin: 3rem auto;
    background-color: #7a7a7a;
}

/* custom button */
.m-button {
    position: relative;
    background-color: var(--secondaryColor);
    text-align: center;
    width: fit-content;
    margin-top: 10px;
    padding: 1rem;
    border-radius: 20px;
    cursor: pointer;
    overflow: hidden;
}

.m-button * {
    position: relative;
    font-weight: bold;
    text-transform: uppercase;
}

/* displays when hovering over the button */
.m-button::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #555;
    transform: translate(-100%, -100%);
    transition-duration: .3s;
    border-radius: inherit;
}

.m-button:hover::before {
    transform: translate(0, 0);
}

/* inactive button */
.m-button.inactive {
    background-color: #555;
    cursor: not-allowed;
}

/* navigation */
.nav-container {
    position: fixed;
    left: 1rem;
    top: 1rem;
    width: 40%;
    height: fit-content;
    z-index: 1;
}

.nav-container.expanded::before {
    content: '';
    position: absolute;
    top: -1rem;
    left: -1rem;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0,0,0,0.5);
}

/* background behind the phone-nav, and li elements under main-nav */
.nav-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 3rem;
    height: 100%;
    border-radius: 50px;
    background-color: rgba(200, 200, 200, .2);
    z-index: -1;
}

/* hamburger menu */
.phone-nav {
    height: 1rem;
    width: fit-content;
    cursor: pointer;
    padding: 15px;
    transition-duration: .5s;
    border-radius: 50%;
}

.phone-nav div {
    width: 16px;
    height: 2px;
    background-color: #c1c1c1;
}

.phone-nav div:first-child,
.phone-nav div:nth-child(2) {
    margin-bottom: 0.20rem;
}

/* navigation list */
.main-nav {
    position: relative;
    width: fit-content;
    height: 0px;
    transition: height .4s;
    overflow: hidden;
}

.nav-container.expanded .main-nav {
    height: 14rem;
}

.main-nav ul {
    position: relative;
    left: -.2rem;
    margin: 0;
    padding: 0;
}

.main-nav ul li {
    position: relative;
    list-style-type: none;
    padding-top: .5rem;
    padding-bottom: .5rem;
    cursor: pointer;
    margin-bottom: 1rem;
    text-transform: uppercase;
    font-size: 0.9rem;
}

.main-nav ul li.active * {
    color: var(--secondaryColor);
    font-weight: 500;
}

.main-nav ul li.active::before {
    content: '';
    position: absolute;
    top: .6rem;
    left: 3rem;
    width: .2rem;
    height: 1.3rem;
    background-color: var(--secondaryColor);
}

.main-nav ul li div {
    width: 10rem;
    position: relative;
    left: 1rem;
}

.main-nav ul li * {
    color: #efefef;
}

.main-nav ul li span {
    display: inline-block;
    font-size: 1rem;
    transform: translateX(0);
    transition-duration: .5s;
}

.nav-container.expanded ul li span {
    margin-left: 1.5rem;
}

.main-nav ul li:hover * {
    color: var(--secondaryColor);
}

.main-nav ul li:hover span {
    transform: translateX(5px);
}

/* main content */
.main-content-container {
    width: 100vw;
    height: 100vh;
    display: flex;
    overflow: hidden;
}

.main-content-container section {
    width: 100vw;
    height: 100vh;
    flex-shrink: 0;
    overflow-y: auto;
    transform: translateX(0);
    transition-duration: .4s;
}

/* header */
#header {
    display: flex;
    align-items: center;
    justify-content: center;
    background: url('../images/background.jpg');
    background-size: 100% 100%;
    background-repeat: no-repeat;
}

#header::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
}

#header .container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    letter-spacing: 1px;
    max-width: 1000px;
    text-transform: uppercase;
}

#header .container .img-container {
    padding: 1.5rem;
}

#header .container .img-container img {
    width: 15rem;
    border-radius: 50%;
    -webkit-box-shadow: 0px 0px 5px 10px rgba(70, 70, 70, 0.5);
    -moz-box-shadow: 0px 0px 5px 10px rgba(70, 70, 70, 0.5);
    box-shadow: 0px 0px 5px 10px rgba(70, 70, 70, 0.5);
}

#header .container .txt-container {
    width: 100%;
}

#header .p1-container {
    margin: 1rem 0;
    text-transform: initial;
}

#header .m-button {
    position: relative;
    margin: auto;
    margin-top: 1.5rem;
    width: 80%;
    height: .5rem;
}

#header .m-button * {
    bottom: 100%;
}

.section-heading {
    margin-right: 10px;
}

.section-heading,
.section-heading + .secondary-color {
    display: inline-block;
    margin-bottom: 2rem;
}

.attr-container h3 {
    display: inline;
}

.attr {
    position: relative;
    white-space: nowrap;
    overflow: hidden;
    margin: auto;
    transition-duration: .3s;
}

.attr.hidden {
    opacity: 0;
}

/* about section */
#about-section .container {
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    max-width: 1500px;
    padding-top: 50px;
    padding-bottom: 25px;
    text-align: center;
    width: 90%;
}

#about-section .container .img-container {
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    width: 80%;
    max-width: 300px;
}

#about-section .container .img-container::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    border: 5px solid var(--secondaryColor);
    width: 100%;
    height: 100%;
}

#about-section .container img {
    position: relative;
    right: 10px;
    bottom: 10px;
}

#about-section .container > span {
    display: block;
    margin: auto;
    margin-bottom: 5rem;
    width: 85%;
    text-align: left;
}

#about-section .info-container {
    margin-top: 5rem;
    text-align: left;
}

#about-section .info-container span {
    color: #f9f9f9;
}

#about-section .info-list {
    margin-top: 20px;
}

#about-section .info-item {
    display: block;
    line-height: 2.5rem;
}

#about-section .info-item .title {
    display: block;
    color: #c1c1c1;
}

#about-section .timelines-container {
    text-align: left;
}

.timelines-container h2 {
    margin-bottom: 2rem;
    font-size: 1.5rem;
}

.timeline-item .date-container span:not(.secondary-color) {
    color: #c1c1c1;
    font-size: .8rem;
}

.timeline-item .date-container .secondary-color {
    font-size: .6rem;
}

.timeline-item .title-container {
    margin: 1.5rem auto;
    text-transform: uppercase;
    font-weight: bold;
}

.timeline-item .desc-container {
    margin-bottom: 2.5rem;
}

.skills-container h2 {
    text-align: left;
    margin-top: 5rem;
}

.skills-container .image-container {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.skills-container .image-container img {
    width: 40%;
    margin-top: 2rem;
    max-width: 150px;
}

/* projects section */
#projects {
    text-align: center;
    overflow-x: hidden;
}

#projects h1 {
    margin-top: 50px;
}

.projects-content {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.project-container {
    width: 95%;
    position: relative;
    margin-bottom: 50px;
    border-radius: 5px;
    background-color: #eeeeee;
    cursor: pointer;
}

.project-container .project-title-container {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--secondaryColor);
    opacity: 0;
    transition-duration: .3s;
}

.project-container .project-title-container:hover {
    opacity: 1;
}

.project-details-container {
    position: sticky;
    top: 0;
    left: 0;
    bottom: 0;
    width: 100vw;
    height: 100vh;
    text-align: left;
    background-color: #333333;
    transform: translateX(100%);
    transition-duration: .3s;
    overflow-y: auto;
    font-size: .9rem;
    z-index: 5;
}

.project-details-container.visible {
    transform: translateX(0);
}

.project-details-container .content {
    padding-top: 100px;
    width: 90%;
    margin: auto;
    padding-bottom: 1rem;
}

.project-details-container .hide-details-container {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 1rem;
    right: 1rem;
    width: 2.5rem;
    height: 2.5rem;
    background-color: rgba(200, 200, 200, .2);
    border-radius: 50%;
    cursor: pointer;
}

.project-details-container .hide-details-container div {
    color: var(--secondaryColor);
    font-weight: bold;
    font-size: 1.5rem;
}

.project-details-container .project-container {
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    cursor: initial;
}

.project-details-container .tech-container {
    margin: 1rem 0;
}

.project-details-container .desc-container .title {
    margin-bottom: 10px;
}

.project-details-container .tech-container div:last-child {
    margin-top: 10px;
}

.project-details-container .tech-container .tech-item {
    text-transform: uppercase;
    font-weight: bold;
}

.project-details-container .desc-container {
    margin-bottom: 1rem;
}

.project-details-container .btns-container .m-button {
    display: inline-block;
    border-radius: 50px;
}

.project-details-container .btns-container .m-button:first-child {
    margin-right: 1rem;
}

/* contact section */
#contact {
    position: relative;
}

.contact-content {
    margin-top: 100px;
    text-align: center;
}

.contact-content * {
    display: inline-block;
}

.contact-content h2 {
    display: block;
    text-align: center;
}

.contact-content *::placeholder {
    color: #f1f1f1;
}

.contact-content input {
    width: 80%;
    height: 30px;
    padding: 10px 5px;
    margin-bottom: 10%;
    outline: none;
    border: none;
    border-radius: 50px;
    text-indent: 10px;
    background-color: #555;
}

.contact-content textarea {
    resize: none;
    width: 80%;
    height: 100px;
    border-radius: 5px;
    margin-bottom: 5%;
    padding: 10px 5px;
    resize: none;
    outline: none;
    border: none;
    text-indent: 5px;
    background-color: #555;
    overflow-x: hidden;
}

.contact-content .m-button {
    width: 60%;
}

/* footers */
.social-media {
    text-align: center;
    margin-top: 1.5rem;
}

.social-media a {
    transition-duration: .3s;
}

.social-media a:nth-child(2) {
    margin-left: 1.3rem;
    margin-right: 1.3rem;
}

.social-media a:nth-child(3) {
    width: 1.3rem;
}

.social-media a * {
    color: #777;
    width: 1.5rem;
}

footer p {
    color: #ffffff4d;
    text-align: center;
    margin-top: 1rem;
}



@media (min-width: 600px) {
    /* header */
    #header .container {
        flex-direction: row;
        align-items: center;
        justify-content: space-evenly;
        text-align: left;
    }

    #header .img-container {
        width: 75%;
    }

    #header .m-button {
        width: 60%;
        margin: initial;
        margin-top: 2rem;
    }

    /* projects section */
    .projects-content .project-container {
        width: 45%;
        margin: 5% 1%;
    }

    .project-details-container .desc-container {
        max-width: 80%;
    }
}

@media (min-width: 1024px) {
    /* about section */
    #about-section .info-container {
        width: 60%;
        margin: 5rem auto;
    }

    #about-section .container > span {
        width: 50%;
    }

    #about-section .info-list {
        display: flex;
        width: 50%;
        margin:  5rem auto;
        flex-wrap: wrap;
        align-items: center;
    }

    #about-section .info-list .info-item {
        width: 30%;
        padding-left: 15%;
    }

    #about-section .timelines-container {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    #about-section .timelines-container > div {
        width: 50%;
        padding-left: 15%;
    }

    .timelines-container .timeline {
        position: relative;
    }

    .timelines-container .timeline::before {
        content: '';
        position: absolute;
        top: 0;
        left: -3rem;
        width: .1rem;
        height: 100%;
        background-color: var(--secondaryColor);
    }

    .timeline .timeline-item {
        position: relative;
    }

    .timeline .timeline-item::before {
        content: '';
        position: absolute;
        top: 0;
        left: -3.45rem;
        width: .7rem;
        height: .7rem;
        background-color: var(--primaryColor);
        border: .15rem solid var(--secondaryColor);
        border-radius: 50%;
    }

    #about-section .skills-container .image-container {
        width: 50%;
        margin: auto;
    }

    #about-section .skills-container .image-container img {
        max-width: 50px;
    }

    /* projects section */
    #projects .project-container {
        width: 30%;
    }

    #projects .project-details-container {
        width: 50%;
        transform: translateX(200%);
    }

   #projects .project-details-container .project-container {
        width: 60%;
    }
    
    #projects .project-details-container.visible {
        transform: translateX(100%);
    }

    #projects.overlayed::before {
    content: '';
    position: absolute;
    top: 0;
    left:0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 200vh;
    background-color: rgba(0,0,0,0.5);
    z-index: 1;
    }
}
